const DEFAULT_IMG_URL = 'http://www.sucaijishi.com/uploadfile/2018/0508/20180508023717621.png'

export default {
  data () {
    return {
      userInfo: {},
      defaultImgUrl: DEFAULT_IMG_URL,
      tabList: [
        {
          name: '待付款',
          iconCls: 'icon-pre-pay',
          index: 1
        },
        {
          name: '待收货',
          iconCls: 'icon-pre-send',
          index: 2
        },
        {
          name: '退款/退货',
          iconCls: 'icon-goods-reject',
          index: 3
        },
        {
          name: '全部订单',
          iconCls: 'icon-all-order',
          index: 0
        }
      ]
    }
  },
  onShow () {
    this.userInfo = wx.getStorageSync('userInfo') || {}
  },
  methods: {
    toLogin () {
      wx.navigateTo({ url: '/pages/login/login' })
    },
    makeCall (str) {
      wx.makePhoneCall({ phoneNumber: str })
    },
    toOrder (index) {
      wx.navigateTo({ url: '/pages/order_list/order_list?activeIndex=' + index })
    }
  }
}
